import React, { useState } from "react";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import { useMutation, useQuery } from "@apollo/client";
import { ADD_TASK } from "../utils/mutations";
import { QUERY_TASKS, QUERY_ME, QUERY_PROJECTS } from "../utils/queries";
import TaskCard from "../components/TaskCard";


export default function SingleProject() {
  const { projectId } = useParams();
  const { data: projectData } = useQuery(QUERY_PROJECTS);
  const projects = projectData?.projects || [];

  const thisProject = projects.filter((project) => project._id === projectId);
  console.log(thisProject[0]);

  return (
    <div>
      <h1>{thisProject[0]._id}</h1>
      <h1>{thisProject[0].projectName}</h1>
      <h1>{thisProject[0].description}</h1>
      <h1>{thisProject[0].completion}</h1>
      <TaskCard />
    </div>
  );
}
